import React, { useState, useEffect } from 'react';
import BottomNav from './BottomNav';
import moment from 'moment';
import { Container, Row, Col } from 'react-bootstrap';
import { Swipeable } from 'react-swipeable';

const TimeTable = ({ local, setLocal }) => {
	const [index, setIndex] = useState((moment().isoWeekday() - 1) % 6);
	const [fakeDate, setFakeDate] = useState(new Date());
	let hour = parseInt(moment().format('H'));
	console.log('hour', hour);

	useEffect(() => {
		setTimeout(() => setFakeDate(new Date()), 60000)
	}, [fakeDate])

	return (
		<Container>
			<Row style={{ minHeight: '100vh', padding: '5rem 1rem' }}>
				<Swipeable
					onSwipedLeft={() => { setIndex((index + 1) % 6) }}
					onSwipedRight={() => { setIndex(index === 0 ? 5 : index - 1) }}
				>
					<Col xs={12} style={{ margin: '2rem 0rem' }}>
						<h1><span>{local[index]['day']}</span></h1>
						<div style={{ background: '#f50057', height: '0.2rem', width: '43%' }}>
						</div>
					</Col>
					{Object.entries(local[index]).map(([key, value]) => {
						if (key === 'day') return null
						let hr = parseInt(key.slice(0, key.indexOf('-')));
						hr = hr <= 5 ? (12 + hr) : hr;

						if (hour === hr && index == moment().isoWeekday() - 1) {
							return (
								<Col xs={12} className='my-4 glow' style={{ ['--color']: 'lightgreen' }} key={key}>
									<h4 className='font-weight-bolder mb-0'>{key}</h4>
									<h5>{value}</h5>
								</Col>
							)
						}
						else if (hr === hour + 1 && index == moment().isoWeekday() - 1) {
							return (
								<Col xs={12} className='my-4 glow' style={{ ['--color']: '#ffd180' }} key={key}>
									<h4 className='font-weight-bolder mb-0'>{key}</h4>
									<h5>{value}</h5>
								</Col>
							)
						}
						else {
							return (
								<Col xs={12} className='my-4' key={key}>
									<h4 className='font-weight-bolder mb-0'>{key}</h4>
									<h5>{value}</h5>
								</Col>
							)
						}
					})}
				</Swipeable>
			</Row>
			<BottomNav setIndex={setIndex} setLocal={setLocal} />
		</Container>
	)
}

export default TimeTable;
